import { 
    Avatar, 
    Badge, 
    Button, 
    Chip, 
    createTheme, 
    Dialog, 
    DialogTitle, 
    Grid, 
    List, 
    Paper, 
    styled 
} from "@mui/material";
import { Box, Stack } from "@mui/system";
import React, { useEffect, useState } from "react";
import GavelIcon from '@mui/icons-material/Gavel';
import ThumbDownIcon from '@mui/icons-material/ThumbDown';
import WhatshotIcon from '@mui/icons-material/Whatshot';
import AutorenewIcon from '@mui/icons-material/Autorenew';
import { ThemeProvider } from "@emotion/react";
import LayersIcon from '@mui/icons-material/Layers';
import PanToolIcon from '@mui/icons-material/PanTool';
import VolunteerActivismIcon from '@mui/icons-material/VolunteerActivism';
import GameAppBar from "./GameAppBar";
import { SimpleDialog } from "./SimpleDialog";

function shuffle(array) {
    let currentIndex = array.length,  randomIndex;
    while (currentIndex != 0) { // While there remain elements to shuffle.
      randomIndex = Math.floor(Math.random() * currentIndex) // Pick a remaining element.
      currentIndex--;
      [array[currentIndex], array[randomIndex]] = [array[randomIndex], array[currentIndex]]       // And swap it with the current element.
    }
    return array;
}

function listOf(n) {
    let list = []
    for (let i = 0; i < n; i++) {
        list.push(null)
    }
    return list
}
  
const StyledBadge = styled(Badge)(({ theme }) => ({
    '& .MuiBadge-badge': {  
      right: 18,
      top: 40,
      border: "none",
      backgroundColor: "transparent",
      color: "gray"
    },
  }));

const RIOT_LIMIT = 3
const MAX_FASCIST_LAWS = 6
const MAX_LIBERAL_LAWS = 5
const CARD_SIZE = 100
const FASCIST_COLOR = "#353535"
const LIBERAL_COLOR = "#4b70bf"
const CARD_COLOR = "#ddd"
export const F = "FASCISTA"
export const L = "LIBERALE"


const cardStyle = {
    textAlign: 'center',
    width: CARD_SIZE,
    height: CARD_SIZE * 2/3,
    elevation: 0,
    borderRadius: 10,
    backgroundColor: CARD_COLOR,
    justifyContent: "center",
    alignItems: "center",
    display: "flex"
}
const liberalCardStyle = {
    textAlign: 'center',
    width: CARD_SIZE,
    height: CARD_SIZE * 2/3,
    elevation: 5,
    borderRadius: 10,
    backgroundColor: LIBERAL_COLOR,
    justifyContent: "center",
    alignItems: "center",
    display: "flex"
}
const fascistCardStyle = {
    textAlign: 'center',
    width: CARD_SIZE,
    height: CARD_SIZE * 2/3,
    elevation: 5,
    borderRadius: 10,
    backgroundColor: FASCIST_COLOR,
    justifyContent: "center",
    alignItems: "center",
    display: "flex"
}
const riotIndicatorStyle = {
    textAlign: 'center',
    width: 25,
    height: 100,
    elevation: 5,
    borderRadius: 100,
}
const buttonStyle = {
    textAlign: 'center',
    height: 50  ,  
    elevation: 5,
    borderRadius: 20,
}

const theme = createTheme({
    palette: {
      mode: "light",

      law: {
        main: '#0A0'
      },
      lawIcon : {
        main: "#eee"
      },
      protest: {
        main: '#b33'
      },
      protestIcon: {
        main: '#eee'
      },
      riot: {
        main: '#d00'
      },
      riotIcon: {
        main: '#eee'
      },
      noDanger: {
        main: '#aaa'
      },
      lowDanger: {
        main: '#FFD700'
      },
      highDanger: {
        main: "#FF8C00"
      },
      liberal_icon: {
        main: "#eee"
      },
      fascist_icon: {
        main: '#a00'
      },
      liberal: {
        main: LIBERAL_COLOR
      },
      fascist: {
        main: FASCIST_COLOR
      },
      shuffle: {
        main: "#99e6b3"
      },

      
    }
  });

const Game = () => {
    return <>
    <ThemeProvider theme={theme}>
        <Board />       
    </ThemeProvider>
    </>
}


const Board = () => {
    
    const initialCards = [
        F, F, F, F, F, F, F, F, F, F, F,
        L, L, L, L, L, L
    ]
    const [cards, setCards] = useState(shuffle(initialCards))
    const [fascistLaws, setFascistLaws] = useState(0)
    const [liberalLaws, setLiberalLaws] = useState(0)
    const [riotLevel, setRiotLevel] = useState(0)
    const [drawMenuOpen, setDrawMenuOpen] = useState(false)
    const [chancellorDialogOpen, setChancellorDialogOpen] = useState(false)
    const [hand, setHand] = useState([])
    const [character, setCharacter] = useState("Presidente")
    const [winner, setWinner] = useState(undefined)
    const [alert, setAlert] = useState({open: false, text: "", title: "", onClose: ()=>{}})

    
    const [players, setPlayers] = useState([
        {name: "Ric",       role: L, isHitler: false, spotted: false},
        {name: "Giulio",    role: F, isHitler: false, spotted: false},
        {name: "Liscio",    role: F, isHitler: true, spotted: false},
        {name: "Lollino",   role: L, isHitler: false, spotted: false},
        {name: "Fede",      role: L, isHitler: false, spotted: false},
        {name: "Lara",      role: F, isHitler: false, spotted: false},
    ])

    const [president, setPresident] = useState(players[0])
    const [chancellor, setChancellor] = useState(undefined)

    const updatePresident = () => {
        let index = undefined
        for (let i=0; i < players.length; i++) {
            if (players[i].name === president.name) {
                index = i+1
                break
            }
        }
        if (index >= players.length) {
            index = 0
        }
        setPresident(players[index])
    }

    const updateChancellor = () => {
        setChancellorDialogOpen(true)
    }

    
    useEffect(() => {
        if (cards) {
            console.log("SHUFFLE: ", cards)
        }
    }, [cards])

    useEffect(() => {
        if (president) {
            setChancellorDialogOpen(true)
            // openAlert("CAMBIO DI MANDATO", "Sale al governo " + president.name, updateChancellor)
        }
    }, [president])

    useEffect(() => {
        if (president && chancellor) {
            
            openAlert("NUOVO GOVERNO", "Presidente " + president.name + "Cancelliere:" + chancellor.name)
        }
    }, [chancellor])

    useEffect(() => {
        if (hand) {
            if (hand.length == 2) {
                setCharacter("Cancelliere")
            }
            if (hand.length == 1) {
                setCharacter("Presidente")
                placeCard(hand[0])
                setHand([])
            }
        }
    }, [hand])

    useEffect(() => {
        if (fascistLaws >= MAX_FASCIST_LAWS) {
            setLiberalLaws(0)
            setFascistLaws(0)
            setWinner(F)
        } else if (liberalLaws >= MAX_LIBERAL_LAWS) {
            setLiberalLaws(0)
            setFascistLaws(0)
            setWinner(L)
        } 

        if (winner) {
            openAlert("FINE PARTITA", "Il gioco è stato vinto dalla fazione " + winner)
        }
        
    }, [liberalLaws, fascistLaws])



    const closeAlert = () => {
        setAlert({...alert, open: false})
    }

    const openAlert = (title, message, onClose=()=>{}) => {
        setAlert({title: title, message: message, open: true, onClose: onClose},)
    }

    const placeCard = (card) => {
        setDrawMenuOpen(false)
        let newLaw = undefined
        if (card === F) {
            setFascistLaws(fascistLaws + 1)
            newLaw = F
        } else if (card === L) {
            setLiberalLaws(liberalLaws + 1)
            newLaw = L
        } else {
            console.error("invalid card", card )
        }
        openAlert("RIFORMA APPROVATA", "La legge appena emendata dal presidente " + president.name + " è di stampo... " + newLaw, updatePresident)
    }


    const draw = () => {
        let card = cards[0]
        let newCards = cards
        newCards.shift()
        setCards(newCards)
        return card
    }

    const newProtest = () => {
        updatePresident()
        if (riotLevel == RIOT_LIMIT) {
            let randomCard = draw()
            placeCard(randomCard)
            setRiotLevel(0)
        } else {
            setRiotLevel(riotLevel + 1)
        }
    }

    const handleDrawCards = () => {
        setHand([cards[0], cards[1], cards[2]])
        setDrawMenuOpen(true)
        let newCards = cards
        newCards.shift()
        newCards.shift()
        newCards.shift()
        setCards(newCards)
    }


    const handleLawSelected = (cardIndex) => {
        if (hand && hand.length > 1) {
            let restCard = []
            for (let i = 0; i < hand.length; i++) {
                if (i != cardIndex) {
                    restCard.push(hand[i])
                }
            }
            setHand(restCard)
        } 
    }

    const shuffleCards = () => {
        let shuffledCards = shuffle(initialCards)
        setCards(shuffledCards)
    }

    


    return (<>    

        <Box container="main"  sx={{width: "90%", margin: 3}}>  

            <Stack direction={"row"} spacing={2}>

                    <Grid container direction="column-reverse" spacing={2} columns={{xs:MAX_LIBERAL_LAWS}} justifyContent="center" alignItems="center">
                        { listOf(fascistLaws)
                            .map((card, i) =>   <Grid item key={i}>
                                                    <Paper style={fascistCardStyle}> 
                                                        <PanToolIcon color="fascist_icon" fontSize="large"/> 
                                                    </Paper>
                                                </Grid> 
                        )}
                        { listOf(MAX_FASCIST_LAWS - fascistLaws)
                            .map((card, i) =>   <Grid item key={i}>
                                                    <Paper style={cardStyle}> 
                                                        <PanToolIcon color="disabled" fontSize="large"/> 
                                                    </Paper>
                                                </Grid> 
                        )}
                    </Grid>

                    <Grid container direction="column-reverse" spacing={2} columns={{xs:MAX_FASCIST_LAWS}} justifyContent="center" alignItems="center">
                        { listOf(liberalLaws)
                            .map((_, i) =>  <Grid item key={i}>
                                                <Paper style={liberalCardStyle}>
                                                    <VolunteerActivismIcon color="liberal_icon" fontSize="large"/>
                                                </Paper>
                                            </Grid> )
                        }
                        { listOf(MAX_LIBERAL_LAWS - liberalLaws)
                            .map((_, i) =>  <Grid item key={i}>
                                                <Paper style={cardStyle}>
                                                    <VolunteerActivismIcon color="disabled" fontSize="large"/>
                                                </Paper>
                                            </Grid>  
                        )}
                    </Grid>

                    <Grid container direction="column-reverse" spacing={2} columns={{xs:RIOT_LIMIT}} justifyContent="center" alignItems="center">
                        {
                            listOf(riotLevel)
                                .map((_, i) => <Grid item key={i}><Chip style={riotIndicatorStyle} variant="contained" color="error"/></Grid> )
                        }
                        {
                            listOf(RIOT_LIMIT - riotLevel)
                                .map((_, i) => <Grid item key={i}><Chip style={riotIndicatorStyle} variant="outlined" color="error"/></Grid> )
                        }
                        <Grid item>
                        { 
                         <WhatshotIcon color={["noDanger", "lowDanger", "highDanger", "riot"][riotLevel]} fontSize="large"/>
                        }  
                        </Grid>
                    </Grid>

            </Stack>



            {/* CONTROL PANEL */}
            <Grid item xs={1} sx={{border:0}}>
                <Stack spacing={3} justifyContent="space-between" direction={"row"} sx={{borderTop: 1, paddingTop: 3, marginTop: 3}}>
                    
                    {/* LAW BUTTON*/}
                    <Button 
                        variant={"contained"}
                        elevation={24} 
                        color={cards.length > 2 ? "law": "shuffle"}
                        style={buttonStyle} 
                        onClick={cards.length > 2 ? handleDrawCards : shuffleCards}
                        startIcon={cards.length > 2 ? <GavelIcon color="lawIcon"/> : <AutorenewIcon color="lawIcon" />}
                        sx={{color: "white", fontSize: 13}}
                    > 
                        {cards.length > 2 ? "RIFORMA": "MISCHIA"}
                    </Button>
                    <Button color="noDanger">
                        <StyledBadge badgeContent={cards.length} color="primary">
                            <LayersIcon fontSize="large"/>
                        </StyledBadge>    
                    </Button>
                    {/* RIOT BUTTON */}
                    <Button 
                        variant={"contained"}
                        color={riotLevel < RIOT_LIMIT ? "protest" : "riot"} 
                        elevation={24} style={buttonStyle} 
                        onClick={newProtest}
                        startIcon={riotLevel < RIOT_LIMIT ? <ThumbDownIcon color="protestIcon" /> : <WhatshotIcon color="riotIcon" />}
                        sx={{color: "white", fontSize: 13}}
                    >
                        {riotLevel < RIOT_LIMIT ? "BOICOTTA" : "RIVOLTA!"}
                    </Button>
                    
                </Stack>
                {/* <SwipeDrawer icon={<GroupsIcon/>}/> */}
                <GameAppBar players={players} president={president} chancellor={chancellor}/>
            </Grid>


            </Box>

            {/* LAW DIALOG */}
            <Dialog 
                open={drawMenuOpen} 
                onClose={() => (hand.length === 1) ? placeCard("FASCIST") : {}} 
                PaperProps={{sx: {height: 200, width: 350, borderRadius:5}}}
                >
                <DialogTitle> { character + president.name + ", scarti una legge"} </DialogTitle>
                <List key={123} sx={{ pt: 0 }}>
                <br />
                <Stack spacing={5} direction="row" justifyContent={"center"} alignItems={"center"}>
                {
                    hand.map((card, i) => <>
                            <Avatar 
                                onClick={() => handleLawSelected(i)} key={card}
                                sx={
                                    card === F ? 
                                      { bgcolor: FASCIST_COLOR, color: "fascistIcon", width: 60, height: 60 } 
                                    : { bgcolor: LIBERAL_COLOR, color: "liberalIcon", width: 60, height: 60 }
                                    
                                }>
                                { card === F ? <PanToolIcon color="fascist_icon"/> : <VolunteerActivismIcon color="liberal_icon"/>}
                            </Avatar>
                    </>)

                }
                </Stack>
                </List>
            </Dialog>


            {/* CHANCELLOR DIALOG */}
            <Dialog 
                open={chancellorDialogOpen} 
                PaperProps={{sx: {height: 500, width: 350, borderRadius:5}}}
                >
                <DialogTitle> 
                    { "Presidente " + president.name + ", nomini un cancelliere "} 
                </DialogTitle>
                <DialogTitle> 
                    { "Presidente " + president.name + ", nomini un cancelliere "} 
                </DialogTitle>
                <List key={123} sx={{ pt: 0 }}>
                <br />
                <Stack spacing={3} direction="column" justifyContent={"center"} alignItems={"center"}>
                {
                    players ?
                    players
                        .filter(player => (player.name != president.name) && (!chancellor || player.name != chancellor.name))
                        .map((player, i) => (
                        <Stack spacing={1} key={i} direction="row" alignItems="center" justifyContent="space-between"
                                onClick={() => {
                                    setChancellor(player);
                                    setChancellorDialogOpen(false)
                                }}>
                            <Button variant="outlined" sx={{ width: 100, height: 50, borderRadius: 5 }}>{player.name}</Button>                            
                        </Stack>
                    ))
                    : <></>

                }
                </Stack>
                </List>
            </Dialog>

            <SimpleDialog open={alert.open} title={alert.title} message={alert.message} onClose={() => {alert.onClose(); closeAlert()}}/>
            
    </>)
}



export default Game;